<template>
    <div class="Practicetest">
        <div class="Practicetest-title">
        <Practicetitle :goStateurl="goStateurl"></Practicetitle>
        </div>
        <router-view :nowdate="nowdate" @changetor="changetorfn" :AnswerGroups="AnswerGroups"></router-view>
        <Praciticefooter @toggledelete="toggledeletefn" @toggleStar="toggleStarfn" :starActive="starActive" :AnswerGroups="AnswerGroups">
        </Praciticefooter>
    </div>
</template>

<style lang="less">
.Practicetest{
.Practicetest-title{
    background: white;
}
}
</style>

<script>
    import Practicetitle from './Practicetitle'
    import Praciticefooter from './Praciticefooter'
    // import PracticeArea from './PracticeArea'

    import driveanswer from '../../../public/driveanswer.json'
    export default {
        name: 'Practicetest',
        components: {
            Practicetitle,
            Praciticefooter,
        },
        watch: {
            "starActive"() {
                
                //获取本地数据
                let Collection = Array.from(new Set(window.localStorage.CollectionQuestion.split(',')))
                //判断是否为空数组
                if (Collection[0] == '') {
                    Collection.splice(0, 1)
                }
                if (this.starActive) {
                    //添加数组
                    Collection.push((this.nowdate.nowId).toString())
                    console.log('收藏')
                    this.choseActive=true
                } 
                else {
                    console.log('取消收藏')
                    //删除数组
                    console.log(this.nowdate.nowId.toString())
                    if (this.choseActive) {
                         Collection.splice(Collection.indexOf(this.nowdate.nowId.toString()), 1)
                    }
                }
                //保存到数据库
                window.localStorage.CollectionQuestion = Array.from(new Set(Collection)).toString()
            },

            "nowdate.nowId"() {
                // 每次清除重复数组
                if (Array.from(new Set(window.localStorage.CollectionQuestion.split(','))).indexOf(this.nowdate.nowId.toString()) != -1) {
                    this.starActive = true
                } else {
                    this.starActive = false
                }
                this.choseActive=false
            }
        },
        data() {
            return {
                goStateurl:'/home',
                starActive: false,
                choseActive:false,
                AnswerGroups: {
                    Answertrue: window.localStorage.Practicetrue, //答对题目数量
                    Answerfalse: window.localStorage.Practiceno, //答错题目数量
                    Driveanswer: driveanswer //答题数据
                },
                nowdate: {
                    nowanswernum: null, //当前题目答案数字
                    nowId: null, //当前题目ID
                    nowquestion: null, //当前题目问题
                    nowlength: [], //当前题目判断是否为选择题，并且储存选择
                    nowurl: null, //当前图片URL
                    PracticeNum: window.localStorage.PracticeNum
                },
            }
        },
        methods: {
            changetorfn(keyname) {
                if (keyname === true) {
                    this.AnswerGroups.Answertrue = window.localStorage.Practicetrue
                } else {
                    this.AnswerGroups.Answerfalse = window.localStorage.Practiceno
                }
            },
            toggleStarfn() {
                this.starActive = !this.starActive
            },
            toggledeletefn(){
                this.deleteActive=true
            }
        }
    }
</script>